<script>
    import { auth } from "./auth.js";
    import PreviewPane from "./PreviewPane.svelte";
    import PreviewModal from "./PreviewModal.svelte";
    import axios from "axios";
    import { onDestroy } from "svelte";
    let auth_state;
    auth.subscribe((auth) => {
        auth_state = auth;
    });
    const unsubscribe = auth.subscribe((auth) => {
        auth_state = auth;
    });
    onDestroy(unsubscribe);
    let data;
    let loaded;
    import baseAPIURLPath from "./baseAPIPath";
    let fileAPIPath = baseAPIURLPath + "fileserver/drive/";
    export let slug;
    var urlArray = [];
    var fileNameArray = []
    $: {
        axios
            .get(baseAPIURLPath + "files/" + slug)
            .then((res) => {
                data = res.data;
            })
            .then(() => {
                loaded = true;
            });
    }
    $ : { if (loaded) {
        data.map((datum) => {
            var temp_url = fileAPIPath + slug + datum.filename;
            urlArray.push(temp_url);
            fileNameArray.push(datum.filename)
        });
    }
    }
    function trash(name) {
        axios
            .delete(baseAPIURLPath + "files" + slug + name, {
                withCredentials: true,
            })
            .then((res) => {
                console.log(res);
                window.location.reload();
            });
    }
</script>

